import React, { useEffect } from 'react';
import { useStaticQuery, graphql, withPrefix, Link } from 'gatsby';
import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';
import { getCurrentLangKey } from 'ptz-i18n';
import { Helmet } from 'react-helmet';
import { FooterProps } from 'rc-footer';
import Seo from '../components/Seo';
import Header from '../components/Header';
import PageLoading from '../components/PageLoading';
import Footer from '../components/Footer';
import * as styles from './layout.module.less';

i18n
  .use(initReactI18next) // passes i18n down to react-i18next
  .init({
    initImmediate: false,
    fallbackLng: 'zh',
    keySeparator: false,
    react: {
      useSuspense: false,
    },
  });

const lngs = ['zh', 'en'];

interface LayoutProps {
  children: React.ReactElement<any>;
  location: Location;
  pageContext: any;
  footerProps: FooterProps;
}

function parseNulltoUndefined<T>(value: T) {
  if (value === null) {
    return undefined;
  }
  return value;
}

const friendLinks = [
  {
    name: 'Alibaba Design',
    link: 'http://www.alibabaued.com/',
    netEnv: 'outter',
  },
  {
    name: 'Alipay Design',
    link: 'https://design.alipay.com/',
    netEnv: 'outter',
  },
  {
    name: 'Alibaba.com设计平台',
    link: 'https://alibaba.fusion.design/',
    netEnv: 'outter',
  },
  {
    name: 'Iconfont',
    link: 'https://www.iconfont.cn/',
    netEnv: 'outter',
  },
  {
    name: '优设网',
    link: 'https://www.uisdc.com/',
    netEnv: 'outter',
  },
  {
    name: 'UI中国',
    link: 'https://www.ui.cn/',
    netEnv: 'outter',
  },
  {
    name: 'Bootstrap',
    link: 'https://www.bootcss.com/',
    netEnv: 'outter',
  },
  {
    name: '淘系前端团队FED',
    link: 'https://fed.taobao.org/',
    netEnv: 'outter',
  },
  {
    name: '阿里妈妈创意中心',
    link: 'https://chuangyi.taobao.com/',
    netEnv: 'outter',
  },
  {
    name: '阿里巴巴前端委员会',
    link: 'https://f2e.alibaba-inc.com/',
    netEnv: 'inner',
  },
  {
    name: '素材中心',
    link: 'https://sucai.alibaba-inc.com/',
    netEnv: 'inner',
  },
  {
    name: 'THX',
    link: 'https://thx.github.io/',
    netEnv: 'outter',
  },
  {
    name: '优设教程网',
    link: 'https://uiiiuiii.com/',
    netEnv: 'outter',
  },
];

const Layout: React.FC<LayoutProps> = ({ children, location, footerProps }) => {
  // https://github.com/gatsbyjs/gatsby/issues/13867#issuecomment-489481343
  if (location.pathname.includes('offline-plugin-app-shell-fallback')) {
    return <PageLoading />;
  }
  const query = graphql`
    query SiteTitleQuery {
      site {
        siteMetadata {
          title
          githubUrl
          siteUrl
          logoUrl
          showSearch
          isAntVSite
          showChinaMirror
          showLanguageSwitcher
          showAntVProductsCard
          showGithubCorner
          navs {
            slug
            title {
              zh
              en
            }
            target
          }
          redirects {
            from
            to
          }
          docsearchOptions {
            apiKey
            indexName
          }
          versions
        }
      }
      locales {
        internal {
          content
        }
      }
    }
  `;
  const { site, locales } = useStaticQuery(query);
  const {
    siteMetadata: {
      title,
      navs = [],
      githubUrl,
      siteUrl,
      logoUrl = '',
      showLanguageSwitcher,
      showSearch,
      showChinaMirror,
      isAntVSite,
      showGithubCorner,
      showAntVProductsCard,
      redirects = [],
      docsearchOptions,
      versions,
    },
  } = site;

  let resources = {};
  try {
    resources = JSON.parse(locales.internal.content);
  } catch (e) {
    // empty
  }
  const pathPrefix = withPrefix('/').replace(/\/$/, '');
  const path = location.pathname.replace(pathPrefix, '');
  const currentLangKey = getCurrentLangKey(lngs, 'zh', path);

  const isHomePage =
    path === '/' ||
    path === `/${currentLangKey}` ||
    path === `/${currentLangKey}/`;

  if (!i18n.options.lng || process.env.NODE_ENV === 'production') {
    i18n.init({
      lng: currentLangKey,
    });
  }

  if (!i18n.options.resources) {
    i18n.init({
      resources,
    });
  }

  useEffect(() => {
    if (i18n.language !== currentLangKey) {
      i18n.changeLanguage(currentLangKey);
    }
  }, [currentLangKey]);

  if (
    location.pathname === pathPrefix ||
    (children && children.type && (children as any).type.noLayout)
  ) {
    return children;
  }

  const getRediectUrl = () => {
    const list = redirects || [];
    for (let i = 0; i < list.length; i += 1) {
      const { from = '', to, keepUrl } = list[i] as {
        from: string | RegExp;
        to: string;
        keepUrl?: boolean;
      };
      // 支持字符串和正则表达式比较
      if (
        location.pathname !== from &&
        !new RegExp(from).test(location.pathname)
      ) {
        return;
      }
      if (keepUrl && new RegExp(from).test(location.pathname)) {
        return location.pathname.replace(new RegExp(from), to);
      }
      // 如果没有指定 to，则直接用替换成老版本的域名
      return to;
    }
  };

  const rediectUrl = getRediectUrl();

  return (
    <>
      {rediectUrl && (
        <Helmet>
          <meta httpEquiv="refresh" content={`0;url=${rediectUrl}`} />
        </Helmet>
      )}
      <Seo
        title={siteUrl === 'https://antv.vision' ? '' : title}
        lang={i18n.language}
      />
      <Helmet>
        <link
          rel="icon"
          type="image/png"
          href="https://img.alicdn.com/imgextra/i2/O1CN01LzsHGM1o2X8CvWnxH_!!6000000005167-2-tps-72-72.png"
        ></link>
      </Helmet>
      <Header
        subTitle={siteUrl === 'https://antv.vision' ? '' : title}
        path={path}
        pathPrefix={pathPrefix}
        navs={navs}
        siteUrl={siteUrl}
        githubUrl={githubUrl}
        Link={Link}
        transparent={isHomePage}
        isHomePage={isHomePage}
        isAntVSite={isAntVSite}
        showSearch={parseNulltoUndefined(showSearch)}
        showGithubCorner={parseNulltoUndefined(showGithubCorner)}
        showAntVProductsCard={parseNulltoUndefined(showAntVProductsCard)}
        showChinaMirror={parseNulltoUndefined(showChinaMirror)}
        showLanguageSwitcher={parseNulltoUndefined(showLanguageSwitcher)}
        docsearchOptions={docsearchOptions}
        versions={versions}
        logo={{
          img: <img src={logoUrl} alt="logo" />,
          link: '/',
        }}
      />
      <main className={styles.main}>{children}</main>
      {!(
        location.pathname.includes('/examples/') &&
        !location.pathname.endsWith('/gallery')
      ) && <Footer links={friendLinks} location={location} {...footerProps} />}
    </>
  );
};

export default Layout;
